<template>
  <div class="text-lg">
    <div class="font-bold">
      <span>{{ label }}</span>
      <span v-if="finishedAt.length" class="text-gray-400">
        <span> | Finished at &dash;</span>
        <span>{{ finishedAt }}</span>
      </span>
    </div>
    <div>{{ min < 10 ? `0${min}` : min }}:{{ sec < 10 ? `0${sec}` : sec }}</div>
  </div>
</template>

<script>
export default {
  name: 'UserTimer',
  props: {
    label: {
      type: String,
      required: true,
    },
    minutes: {
      type: Number,
      required: true,
    },
    seconds: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      min: this.minutes,
      sec: this.seconds,
      finishedAt: '',
      interval: null,
    };
  },
  created() {
    this.initTimer();
  },
  methods: {
    initTimer() {
      this.interval = setInterval(this.timerTick, 1000);
    },

    timerTick() {
      // —É–ø—Ä–æ—â—ë–Ω–Ω—ã–π –≤–∞—Ä–∏–∞–Ω—Ç - –≤—ã—á–∏—Ç–∞–µ–º –∫–∞–∂–¥—ã–π —Ä–∞–∑ –ø–æ —Å–µ–∫—É–Ω–¥–µ
      // —Ö–æ—Ç—è setInterval –∏ setTimeout –Ω–µ –≥–∞—Ä–∞–Ω—Ç–∏—Ä—É—é—Ç –≤—ã–ø–æ–ª–Ω–µ–Ω–∏–µ
      // –≤ —Ç–æ—á–Ω–æ—Å—Ç–∏ —á–µ—Ä–µ–∑ —É–∫–∞–∑–∞–Ω–Ω–æ–µ –≤—Ä–µ–º—è

      if (this.sec > 0) {
        this.sec -= 1;
      } else if (this.min > 0) {
        this.min -= 1;
        this.sec = 59;
      } else {
        const now = new Date();
        this.finishedAt = now.toLocaleTimeString('ru');

        clearInterval(this.interval);
        this.interval = null;
      }
    },
  },
};
</script>

